.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.form-control {
  background-color: #f3f4f6; 
  border: 1px solid #ddd; 
  border-radius: 5px; 
  padding: 10px 15px;
}

.form-label {
  margin-bottom: 5px;
  font-weight: bold;
  color: #333;
}

.btn-primary {
  background-color: #f00e0e;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
}

.btn-primary:hover {
  background-color: #bb1818; 
}

.container {
  max-width: 600px;
  margin: auto; 
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1); 
  border-radius: 8px; 
}